@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
:root {
	--pagecolor: #242837;
	--finished: #006400;
	--todo: #662d91;

	--textColorInverse: #f4f1fa;
	--textColor: #f4f1fa;

	--taskBackground: #1f222f;
	--containerBorder: steelblue;
	--remove-btn-color: #ca0b00;
	--edit-btn-color: #f0d500;
	--inputBackground: #1f222f;
	--inputHeight: 34px;
	--inputWidth: 100%;
	--borderRadius: 3px;
	--inputHover: #662d91;
	--usefulOrange: #fd5618;
	--buttonRadius: 7px;
}

html[data-theme='light'] {
	--textColor: black;
	--inputBackground: #e9e3f4;
	--containerBorder: orangered;
	--taskBackground: #e9e3f4;
	--pagecolor: #f4f1fa;
}

html,
body {
	background-color: #242837;
	background-color: var(--pagecolor);
	font-family: 'Poppins', sans-serif;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	transition: 200ms;
}

.task-container:not(:last-child) {
	border-bottom: 1px solid var(--inputHover);
	margin-bottom: 20px;
}

.task-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	margin-bottom: 10px;
}

.task-title {
	color: var(--textColor);
}

.task-content {
	overflow: hidden;
	width: 960px;
}

.task-title.completed {
	color: green;
}

.task-container.completed {
	border-bottom: 1px solid darkgreen !important;
}

.button {
	border-radius: 4px;
	padding: 6px;
	background-color: transparent;
	border: 1px solid dodgerblue;
	color: var(--textColor);
	cursor: pointer;
	outline: none;
}

.button.remove {
	background-color: var(--remove-btn-color);
	border: none;
	margin-left: 9px;
	color: var(--textColorInverse);
}

.button.edit {
	border: 1px solid #662d91;
	background: transparent;
	transition: background-color 0.3s;
	outline: none;
}

.button.edit:hover {
	background-color: #613dc1;
}

@media only screen and (max-width: 700px) {
	.button.remove {
		margin-left: 0;
		width: 100%;
		margin: 7px 0;
	}
	.button.edit {
		width: 100%;
	}
}

@media only screen and (max-width: 1200px) {
	.task-content {
		max-width: 80%;
	}
}

.task-list-container {
	padding: 15px 20px;
	background: var(--taskBackground);
	border-radius: 7px;
}

.filter {
	display: flex;
	justify-content: flex-end;
	margin-bottom: 30px;
	color: var(--textColor);
}

.filter-content {
	display: flex;
}

button.finished {
	margin-left: 7px;
	background: var(--finished);
	border: 1px solid var(--finished);
	color: var(--textColorInverse);
}

button.todo {
	margin-left: 7px;
	background: var(--todo);
	border: 1px solid var(--todo);
	color: var(--textColorInverse);
}

button.unfiltered {
	margin-left: 7px;
	background: var(--usefulOrange);
	border: 1px solid var(--usefulOrange);
	color: var(--textColorInverse);
}

.filter-btn-container {
	margin-left: 18px;
}

@media only screen and (max-width: 600px) {
	.button.finished {
		margin-left: 7px;
	}
	.button.todo {
		margin-left: 7px;
	}
	.button.unfiltered {
		margin-left: 7px;
	}
}

.input {
	margin: 10px 0;
	display: block;
}

.label-box {
	display: block;
	color: var(--textColor);
	font-size: 18px;
}

.text-box {
	background-color: var(--inputBackground);
	outline: none;
	border: transparent;
	border-radius: var(--borderRadius);
	width: var(--inputWidth);
	height: var(--inputHeight);
	color: var(--textColor);
	padding: 0 5px;
}

.text-box:focus {
	border-bottom: 1px solid var(--inputHover);
}

.required {
	border-bottom: 1px solid var(--remove-btn-color);
}

.add-todo-container {
	margin-bottom: 60px;
}

.todo-create-container {
	margin: 10px 0;
}

.task-tracker-container {
	display: flex;
	justify-content: space-between;
	margin-bottom: 40px;
}

.task-tracker {
	color: var(--textColor);
	font-size: 24px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.btn {
	padding: 7px 15px;
	border-radius: var(--buttonRadius);
	outline: none;
	background-color: transparent;
	color: var(--textColor);
	border: 1px solid var(--usefulOrange);
	transition: background-color 0.3s;
	font-size: 18px;
	cursor: pointer;
	margin-left: 15px;
}

.btn:hover {
	background-color: var(--usefulOrange);
}

.cancel {
	border: 1px solid var(--remove-btn-color);
	margin-left: 10px;
}

.cancel:hover {
	background: var(--remove-btn-color);
}

.edit {
	border: 1px solid #3ec300;
}

.edit:hover {
	background: #3ec300;
}

.margin {
	margin-left: 5px;
}

.theme-button {
	border-radius: 4px;
	padding: 3px;
}

.theme-button {
	background-color: transparent;
	outline: none;
	border: none;
	font-size: 20px;
	color: yellow;
}

html[data-theme='light'] .theme-button {
	color: black;
}

.btns-container-add {
	display: flex;
	align-items: center;
	justify-content: center;
}

.wrapper {
	border-radius: 9999px;
	background-color: #4d4d4d;
	padding: 10px;
	width: 50px;
	height: 24px;
	display: flex;
	justify-self: space-between;
	align-items: center;
	position: relative;
	box-sizing: border-box;
	cursor: pointer;
}
span {
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	font-size: 15px;
}
.moon-wrapper {
	position: absolute;
	left: 2px;
}
.sun-wrapper {
	position: absolute;
	right: 2px;
}
.thumb {
	width: 21px;
	height: 21px;
	background-color: #fff;
	border-radius: 9999px;
	position: absolute;
	transform: translateX(-9px);
	transition: 300ms;
}
.thumb.focused {
	box-shadow: 0 0 2px 3px #25c2a0;
}

.wrapper:hover .thumb {
	box-shadow: 0 0 2px 3px #25c2a0;
}

.thumb.toggled {
	transform: translateX(18px);
}

.container {
	max-width: 1200px;
	margin: 30px auto;
	overflow: auto;
	min-height: 500px;
	border: 1px solid var(--containerBorder);
	padding: 28px;
	border-radius: 5px;
}

@media only screen and (max-width: 600px) {
	.container {
		border: none;
	}
}

